<template>
  <ul
    class="filter__list"
    :class="{'filter__list--swatches': facet.name === 'named_tags.color'}"
  >
    <li
      v-for="list in items"
      :key="list.title"
      class="filter__item checkbox__container"
      :data-value="encodedValue"
      @click.prevent="refine(list.value)"
    >
      <input
        :id="encodedValue"
        type="checkbox"
        class="checkbox__input"
        :aria-checked="list.isRefined"
        tabindex="0"
        :checked="list.isRefined"
      >

      <template v-if="facet.name === 'named_tags.color'">
        <label
          class="checkbox__swatch"
          :for="encodedValue"
        >
          <img
            class="product-card-swatches__image"
            :alt="list.label"
            :title="list.label"
            :src="sanitizedSwatchUrl(list.label)"
          >
        </label>
      </template>
      <template v-else>
        <label
          class="checkbox__label"
          :for="encodedValue"
        >
          {{ formatLabel(list.label) }}
          <span class="filter__count">&nbsp;({{ list.count }})</span>
        </label>
      </template>
    </li>
  </ul>
</template>
<script>

/**
 * Vue: Filter list (filter-list)
 * -----------------------------------------------------------------------------
 * Collection Algolia filter
 *
 */
import { handleize } from '~/helpers/general'

export default {
  name: 'FilterList',

  props: {
    facet: {
      type: [Array, Object],
      default: () => ({}),
    },
    items: {
      type: Array,
      default: () => [],
    },
    refine: {
      type: Function,
      default: () => ({}),
    },
  },

  data() {
    return {

    }
  },

  computed: {
    encodedValue() {
      return encodeURIComponent(this.facet.title)
    },
  },

  methods: {

    /**
     * Format label price.
     * @param {String} label - Facet label.
     * @return {String}
     */
    formatPriceLabel(label) {
      if (label.length === 3) {
        return this.$formatMoney(`${label}0`)
      }

      return this.$formatMoney(label)
    },

    /**
     * Format price range label.
     * @param {String} label - Facet label.
     * @return {String}
     */
    formatPriceRange(label) {
      return label.split(':').map(item => this.formatPriceLabel(`${item}.00`)).join(' - ')
    },

    /**
     * Format facet price and price range
     * @param {String} label - Facet label.
     * @return {String}
     */
    formatLabel(label) {
      if (this.facet.name === 'price') {
        return this.formatPriceLabel(label)
      }

      if (this.facet.name === 'price_range') {
        return this.formatPriceRange(label)
      }

      return label
    },

    /**
     * Sanitized swatch url
     * @param {String} color - Swatch color.
     * @return {String}
     */
    sanitizedSwatchUrl(color) {
      return cnvs.urls.files
          .replace('placeholder', `swatch_${handleize(color)}_20x20.png`)
    },
  },
}
</script>

